import React, { Component } from 'react'
import request from '@/utils/request'
import Result from '@/component/result'
export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: [],
      user1: new URLSearchParams(props.history.location.search).get('player1'),
      user2: new URLSearchParams(props.history.location.search).get('player2') || '',
    }
  }
  async componentDidMount() {
    let { user1, user2 } = this.state
    if (!user1 || !user2) {
      this.props.history.push('/battle')
    } else {
      let s1 = '',
        s2 = '',
        list = []
      await Promise.all([
        request(`https://api.github.com/users/${user1}`)
          .then((res) => {
            s1 = res
          })
          .catch(() => {
            this.setState({
              loading: false,
            })
          }),
        request(`https://api.github.com/users/${user2}`)
          .then((res) => {
            s2 = res
          })
          .catch(() => {
            this.setState({
              loading: false,
            })
          }),
      ])
      if (s1.public_repos > s2.public_repos) {
        s1.result = 'Winner'
        s2.result = 'Loser'
      } else if (s1.public_repos === s2.public_repos) {
        s1.result = 'Draw'
        s2.result = 'Draw'
      } else {
        s1.result = 'Loser'
        s2.result = 'Winner'
      }
      list.unshift(s1, s2)
      this.setState({ data: list, loading: false })
    }
  }
  render() {
    let { loading, data = [] } = this.state
    return <div className="result-page">{<Result data={data} {...this.props} loading={loading} />}</div>
  }
}
