import React, { Component } from 'react';
import { Button, Spin } from 'antd'
import './result.less'
class Result extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
    let { data, loading } = this.props;
    return (
      <div className="result-page">
        <Spin spinning={loading} tip="Loading...">
        <div className="result-content"
        >
          {data.length ?
            data.map((item, i) => (
              <div
                key={i}
              >
                <h2>{item.result}</h2>
                <img
                  src={item.avatar_url ? item.avatar_url : require('@/assets/img/user.png')}
                  alt=""
                />
                <h4>Scores: {item.public_repos}</h4>
                <h4>{item.name == null ? item.login : item.name}</h4>
                <p>{item.location || '--'}
                </p>
                <p>{item.followers}</p>
                <p>{item.following}</p>
                <p>{item.public_repos}
                </p>
              </div>
            ))
          : null}
        </div>
        <div className='reset-btn'><Button
          type='primary'
          onClick={this.reBack}
        >Reset
        </Button></div>
        </Spin>
      </div>
    );
  }
  reBack = () => {
    this.props.history.push('/battle')
  }
}

export default Result;
